import './input.css';

export function InputForm(props){

    const onChange = (object) => props.setText(object.target.value);

    const makeUpper = ()=> props.setText(props.text.toUpperCase());
    const makeLower = ()=> props.setText(props.text.toLowerCase());

    const showAnalysis = () => document.querySelector('.reveal').style.display = 'block';

    function copyText(){
        const textField = document.querySelector('textarea');
        textField.select();
        navigator.clipboard.writeText(textField.value);
    }

    function deleteText(){ 
        props.setText('');
        document.querySelector('.reveal').style.display = 'none';
    }

    return <div className="container">
        <label htmlFor="test" style={{ color: (props.mode === 'light') ? 'black': 'white'}}>Paste or write the content inside given block</label>
        <textarea id="test" type='text' cols="73" rows="11" onChange={onChange} value={props.text}></textarea>
        <div className="buttons">
            <button onClick={showAnalysis}>Submit text</button>
            <button onClick={deleteText}>Clear text</button>
            <button onClick={makeUpper}>Convert to Uppercase</button>
            <button onClick={makeLower}>Convert to Lowercase</button>
            <button onClick={copyText}>Copy text</button>
        </div>
    </div>
}
