import './report.css';

export function Report(props){
    

    return <div className="container reveal" style={{color : (props.mode === 'light') ? 'black' : 'white'}}>
        <h2>Your Text Summary</h2>
        <div className="block" style={{border : (props.mode === 'light') ? '0.2vw solid black' : '0.2vw solid white'}}>
            <span>No. of chars (with spaces)  :   {props.text.replace('\n', '').length}</span>
            <span>No. of chars (without spaces)  :   {props.text.split(' ').join('').replace('\n', '').length}</span>
            <span>No. of words  :   {props.text.length === 0 ? 0: props.text.replace(/\s+/g, " ").trim().replace('\n', '').split(' ').length}</span>
            <span>No. of sentences  :   {props.text.length === 0 ? 0:props.text.split('\n').length}</span>
        </div>
    </div>;
}