import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";

import {useState} from 'react';

import {Home, Services, Team, Contact} from './page_routes/page_routes';

import {Navbar as Nav} from './components/components';


export default function Analyzer(){

  const [text, setText] = useState('');

  const [mode, setMode] = useState('light');

  return <>
    <Router>
      
      <Nav text={text} setText={setText} mode={mode} setMode={setMode} />

      <Routes>                         

        <Route path='/' element={<Home text={text} setText={setText} mode={mode} />} />

        <Route path='/services' element={<Services  />} />
        <Route path='/team' element={<Team  />} />
        <Route path='/contact' element={<Contact  />} />


      </Routes>


    </Router>
  </>;
}