import './navbar.css';
import ToggleButton from '../toggle_button/button';
import {Link} from 'react-router-dom';

export function Navbar(props){


    return <div className="navbar" style={(props.mode === 'light')? {
        backgroundColor: 'rgb(249, 244, 244)',
        color: 'black'
    } : {
        backgroundColor: '#280202',
        color: 'white'
    } }>
        <span>Text-Analyzer</span>
        <div className='links'>
            <Link style= {{color: (props.mode === 'light') ?'black' : 'white'}} to='/'>Home</Link>
            <Link style= {{color: (props.mode === 'light') ?'black' : 'white'}} to='/services'>Services</Link>
            <Link style= {{color: (props.mode === 'light') ?'black' : 'white'}} to='/team'>Our Team</Link>
            <Link style= {{color: (props.mode === 'light') ?'black' : 'white'}} to='/contact'>Contact Us</Link>
        </div>
        <ToggleButton mode={props.mode} setMode={props.setMode} /> 
        <span className="mode">Enable Dark mode</span>
    </div>;
}