import {InputForm as Form, Report} from '../components/components';


export function Home(props){        

        document.querySelector('body').style.background = (props.mode === 'light')? 'lightyellow': 'url(https://www.wallpapertip.com/wmimgs/61-610111_galaxy-wallpaper-cartoon-free.jpg)';
        document.querySelector('body').style.backgroundSize = 'cover';
    
    
    return <>
        <Form text={props.text} setText={props.setText} mode={props.mode}></Form>
        <Report text={props.text} mode={props.mode}></Report>
    </>
}

